.avatar-container {
    transition: background-color 0.3s ease;

    display: block;
    position: relative;
    resize: horizontal;
    overflow: auto;

    box-sizing: border-box;

    min-width: 2rem;
    max-width: 100%;

    min-height: 2rem;
    height: auto;
    max-height: 20rem;

    padding: 1rem 1rem 3rem 1rem;
    border: 0.125rem dashed rgb(var(--contrast-500));

    border-radius: 0.5rem;

    &:after {
        content: 'Resize me â¤µ';
        font-size: 0.75rem;
        position: absolute;
        right: 0.25rem;
        bottom: 0.25rem;
    }
}

limel-lime-ai-avatar {
    margin: auto;
    border-radius: 50%;

    &:hover {
        --lime-ai-avatar-animation-play-state: running;
    }
}
