import { Component, h, State } from '@stencil/core';

/**
 * With background colors
 * To render better, the AI avatar needs to be placed on a colored background.
 */
@Component({
    tag: 'limel-example-lime-ai-avatar-colors',
    shadow: true,
    styleUrl: 'lime-ai-avatar-basic.scss',
})
export class LimeAiAvatarColorsExample {
    @State()
    private backgroundColor: string = 'rgb(var(--color-blue-default))';

    public render() {
        return [
            <div class="avatar-container">
                <limel-lime-ai-avatar
                    style={{ backgroundColor: this.backgroundColor }}
                />
            </div>,
            <limel-color-picker-palette
                label="Background Color"
                helperText="Select a background color for the AI avatar"
                value={this.backgroundColor}
                onChange={this.handleColorChange}
            />,
        ];
    }

    private handleColorChange = (event: CustomEvent<string>) => {
        this.backgroundColor = event.detail;
    };
}
